.fixedTabs{
    position: fixed;
    right: 1rem;
    bottom: 2rem;
    z-index: 200;
}
.fixedTabs a{
    background-color: #64ac57;
    font-size: 2rem;
    height: 5rem;
    width: 4rem;
    color: white;
    padding: 0.2rem 1rem 1.3rem 1rem;
    border-radius: 4rem;
    line-height: 4.5rem;
    box-shadow: 0px 0px 15px lightgrey;
}
.fixedTabs a:hover{
    color: white;
}
.slick-dots-bottom li{
    background-color: #d5d5d5;
}
.slick-active button{
    background-color: grey !important;
}
.ant-carousel .slick-slide h3 {
    color: #fff;
}
.banner{
    width:100%
}
.customeContainer{
    padding: 0% 8%
}
.categories{
    padding: 2rem 8% 0rem 8%;
}
.categoryIcon{
    width: 110%;
    padding: 35% 0 0 17%;

}
.categoryBox{
    box-shadow: 0px 0px 15px #dedede;
    border-radius: 14px;
    margin-bottom: 2rem;
}
.categoryBox img{
    padding-bottom: 2rem;
}
.subcategoryList{
    list-style-type: none;
    width: 100%;
}
.categoryName{
    padding: 1rem 0rem 0rem 0rem;
    font-weight: bold;
    font-size: 1.1rem;
}
.subcategories{
    font-size: 0.8rem;
    font-weight: lighter;
}
.categoryBox a{
    color: #222222;
}
.categoryBox a:hover{
    text-decoration: underline;
}
.middlemanOnboardingIntro{
    /*background-image: url("../assets/back3.jpg");*/
    background-size: cover;
}
.howWeWorkSection{
    height: 80vh;
}
.middlemanSection{
    background-image: linear-gradient(to bottom right,#81bcbe, #1b96f9);
}
.middlemanSection{
    margin: 2rem 8%;
    margin-bottom: 2rem;
    padding: 2rem;
}
.middlemanSectionHead{
    font-weight: bolder;
    font-size: 3rem;
    color: white;
    line-height: 4rem;
}
.middlemanSectionText{
    font-size: 1.2rem;
    color: white;
    margin: 1rem 0rem;
}
.middlemanSectionButton button{
    background-color: white;
    color: #5daca7;
    font-size: 1.2rem;
    padding: 1rem 3rem;
    height: auto !important;
    margin-top: 2rem;
    margin-bottom: 2rem;
    border-radius: 5rem;
    border: none;
    border: 2px solid white;
}
.middlemanSectionButton button:hover,
.middlemanSectionButton button:after,
.middlemanSectionButton button:before{
    background-color: transparent;
    border: 2px solid   white;
    color: white;
}

.trendingProducts{
    margin: 2rem 8%;
    background-color: #f6f6f6;
    padding: 1.5rem;
}
.trendingProducts .col-md-2{
    padding: 0.3rem !important;
}
.trendingProduct{
    padding: 0.5rem;
    border-radius: 12px;
    /*border: 1px solid #d7d7d7;*/
    background-color: white;
}
.trendingCarousalImage{
    max-height: 200px;
    background-color: #eaeaea;
}
.trendingCarousalImage img{
    height: 200px;
    overflow: hidden;
    width: 100%;
    display: block;
    margin-left: auto;
    margin-right: auto;
}
.trendingProductName{
    font-weight: bold;
    line-height: 1.1rem;
    margin-top: 0.3rem;
}
.trendingProduct a{
    color: #222222;
}
.trendingProductPrice{
    font-size: 1.2rem;
    font-weight: bold;
    color: #ee3243;
    font-family: Arial;
}
.trendingProductButton{
    background: linear-gradient(to bottom right, #1b96f9, #1b96f9);
    color: white;
    padding: 0.4rem;
    text-align: center;
    font-size: 1rem;
    border-radius: 5px;
    border: 1px solid #48a4e3;
}
.trendingProductButton:hover{
    cursor: pointer;
    border: 1px solid #48a4e3;
    background: linear-gradient(to bottom right, white, white);
    color: #48a4e3;
}
.trendingProductsHead{
    font-size: 1.4rem;
    margin-bottom: 0.2rem;
    color: black;
}
.trendingProductsHeadText{
    margin-bottom: 0.7rem;
}
.footer{
    background-color: #f3f3f3;
    padding: 4rem 10% 1rem 10%;
}
.footerContactSectionHead{
    font-size: 1.5rem;
}
.footerContactSectionText{
    padding: 0.2rem 0rem;
}
.copyrightText{
    text-align: center;
    padding: 4em 0rem 0rem 0rem;
}
.footerLogo img{
    width: 150px;
    filter: grayscale(1);
}
.footerList{
    margin-top: 1rem;
}
.footerList a{
    color: #3c3c3c;
    margin-left: 0.3rem;
    line-height: 1.7rem;
}
.whatWeOfferSection{
    padding: 2rem 8%;
    text-align: center;
    margin: 4rem 0rem 6rem 0rem;
}
.aboutIcon img{
    width: 20%;
}
.aboutHead{
    font-size: 2rem;
}
.aboutSubHead{
    margin-bottom: 4rem;
}
.aboutIconHead{
    font-weight: bold;
    margin-top: 1rem;
}
@media only screen and (max-width: 700px){
    .categories{
        padding: 2rem 6% 0rem 8%;
    }
    .categoryBox{
        margin-bottom: 0.5rem !important;
    }
    .categoryBox img{
        padding: 33% 0rem 1rem 1rem;
    }
    .categoryBox .col-md-4{
        width: 35% !important;
        display: inline-block;
    }
    .categoryBox .col-md-7{
        width: 65% !important;
        display: inline-block;
    }
    .categoryIcon{
        width: 100%;
        padding: 0 0 0 17%;

    }
    .categoryBox{
        box-shadow: 0px 0px 15px #dedede;
        border-radius: 14px;
        margin-bottom: 2rem;
    }
    .subcategoryList{
        list-style-type: none;
        width: 100%;
    }
    .categoryName{
        padding: 1rem 0rem 0rem 0rem;
        font-weight: bold;
        font-size: 1.1rem;
    }
    .subcategories{
        font-size: 0.8rem;
        font-weight: lighter;
    }
    .categoryBox a{
        color: #222222;
    }
    .categoryBox a:hover{
        text-decoration: underline;
    }
    .trendingCarousalImage{
        max-height: 250px;
        background-color: #ffffff;
    }
    .trendingCarousalImage img{
        height: 250px;
        overflow: hidden;
        width: 250px;
        display: block;
        margin-left: auto;
        margin-right: auto;
    }
    .footerList{
        margin-left: 0.3rem;
    }
    .footerContactSectionHead{
        margin-left: 0.5rem;
    }
    .footerContactSectionText{
        margin-left: 0.5rem;
    }
    .aboutSection{
        padding-top: 1rem;
    }
}