.addProductContainer{
    background-color: #f4f4f4;
    padding: 2em;
}
.addProductHead{
    font-size: 1.8rem;
}
.divider{
    background-color: #cbcbcb;
}
.productImmage{
    display: inline-block;
    margin-top: 1rem;
    width: 150px;
}
.productImmage img{
    width: 150px;
    padding: 1rem 1rem 1rem 0rem;
}
.removeImage{
    background-color: red;
    display: inline-block;
    color: white;
    width: 1.5rem;
    height: 1.5rem;
    border-radius: 50%;
    font-size: 0.8rem;
    position: absolute;
    padding-left: 5px;
    left: 135px;
    z-index: 200;
}
.removeImage:hover{
    cursor: pointer;
}
.productPageIcon{
    position: absolute;
    right: 2rem;
    top:2rem;
    font-weight: bold;
    font-size: 1.5rem;
    transform: rotate(90deg);
    color: black !important;
}
@media only screen and (max-width: 700px) {
    .addProductHead{
        font-size: 1.5rem;
        width: 90%;
    }
}
