.loginForm{
    width: 100%;
    margin-top: 20%;
    box-shadow: 0px 0px 15px #dcdcdc;
    border-radius: 15px;
    margin-bottom: 20%;
}
.loginFormHeader{
    background-image: linear-gradient(to bottom right,#81bcbe, #1b96f9);
    padding: 1.5rem;
    border-radius: 15px 15px 0px 0px;
}
.loginHeaderText{
    font-size: 2rem;
    color: white;
}
.loginHeaderText2{
    color: white;
}
.loginForm form{
    padding: 1.5rem;
}
.fileName{
    margin-left: 0;
    width: 100% !important;
}
@media only screen and (max-width: 700px) {
    .loginForm{
        width: 90%;
        margin-left: 5%;
        margin-top: 10%;
        box-shadow: 0px 0px 15px #dcdcdc;
        border-radius: 15px;
        margin-bottom: 10%;
    }
    .loginHeaderText{
        font-size: 1.5rem;
    }
}