.sellerStat{
    box-shadow: 0px 0px 15px #e2e2e2;
    border-radius: 12px;
    padding: 2rem;
    text-align: center;
    margin-top: 3rem;
}
.statNumber{
    font-size: 2.5rem;
    font-weight: bold;
}
.tableTitle{
    text-align: left;
    margin-top: 3rem;
    margin-bottom: 1rem;
    font-size: 1.5rem;
}
.addSellerContainer{
    box-shadow: 0px 0px 15px #dadada;
    border-radius: 15px;
    margin-bottom: 6rem;
}
.addSellerSubmitButton{
    margin-left: 50%;
}
@media only screen and (max-width: 700px) {
    .addProductContainer{
        margin-bottom: 4rem;
    }
    .addSellerSubmitButton{
        margin-left: 0;
    }
    .sellerStat{
        margin-top: 1rem;
    }
}