.productTab{
    background-color: white;
    padding:1rem;
    box-shadow: 0px 0px 15px #e8e8e8;
    min-height: 40vh;
}
.productTabName{
    font-size: 1.7rem;
    font-weight: bold;
}
.productTabPrice{
    font-size: 1.2rem;
    color: #bd3c35;
    font-weight: bold;
    font-style: italic;
}
.productTabDescription{
    margin-bottom: 1rem;
    font-size: 1rem;
}
.productTabDetailsLabel{
    font-weight: bold;
    margin-right: 1rem;
    width: 150px;
}
.productTabDetailsLabel, .productTabDetailsValue{
    display: inline-block;
}
.deliveryInstruction{
    margin: 0rem 0rem 0rem 0rem;
}
.descriptionSection{
    padding: 1rem;
}
.certified{
    margin: 1rem 0rem;
}
.certified img{
    width: 70px;
    transform: rotate(-45deg);
}
.productImages{
    padding: 1rem;
}
.productImageFull{
    padding: 1rem 1rem 1rem 0rem;
    display: inline-block;
}
.productImageFull img{
    width: 100%;
    display: block;
    margin-left: auto;
    margin-right: auto;

}
.productImageFull{
    display: inline-block;
}
.productImagesTitle{
    font-size: 1rem;
    color: black;
    font-weight: bold;
}
.productTabMovingImage{
    height: 300px;
    align-content: center;
    text-align: center;
    background-color: #f0f0f0;
}
.productTabMovingImage img{
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: auto !important;
    height: 100%;
}
@media only screen and (max-width: 700px) {
    .productImageFull img{
        width: 100%;
    }
    .productImageFull{
        padding: 1rem 0rem 0rem 1rem;
        display: inline-block;
    }
    .descriptionSection{
        padding: 1rem 1rem 1rem 0rem;
    }
    .certified{
        margin: 1rem 0rem ;
    }
    .certified img{
        width: 70px;
        transform: rotate(-45deg);
    }
}