.viewMore{
    margin: 1rem 0rem;
    font-weight: 500;
    text-decoration: underline  ;
}
.categoryProductImage{
    height: 250px;
    background-color: #eaeaea;
}
.categoryProductImage img{
    height: 100%;
    width: auto !important;
    display: block;
    margin-left: auto;
    margin-right: auto;
    max-width: 100%;
}
.productTab{
    border-bottom: 1px solid lightgrey;
}
@media only screen and (max-width: 700px) {
    .productTab{
        margin: 1rem;
    }
    .productTabDetailsLabel, .productTabDetailsValue{
        display: none;
    }
}