.signupForm{
    width: 100%;
    margin-top: 4rem;
    box-shadow: 0px 0px 15px #dcdcdc;
    border-radius: 15px;
    margin-bottom: 5rem;
}
.signupForm form{
    padding: 1.5rem;
}
.fileName{
    margin-left: 0;
    width: 100% !important;
}
@media only screen and (max-width: 700px) {
    .signupForm{
        width: 90%;
        margin-left: 5%;
        margin-top: 10%;
        box-shadow: 0px 0px 15px #dcdcdc;
        border-radius: 15px;
        margin-bottom: 10%;
    }
}