.subCategoryList{
    margin-bottom: 2rem;
}
.subCategoryItem{
    display: inline-block;
    padding: 0.4rem 1rem;
    margin-right: 1rem;
    border-radius: 50px;
    border: 1px solid #48a4e3;
    color: #48a4e3;
}
.subCategoryItem:hover{
    cursor: pointer;
}
.emptyProducts{
    margin: 15% 0 25% 0;
}
.allProducts{
    min-height: 40vh;
}
@media only screen and (max-width: 700px) {
    .subCategoryItem{
        display: inline-block;
        padding: 0.3rem 0.6rem;
        margin-right: 0.5rem;
        border-radius: 50px;
        border: 1px solid #48a4e3;
        color: #48a4e3;
        font-size: 0.8rem;
        margin-bottom: 0.7rem;
    }
    .subCategoryList{
        margin-bottom: 1rem;
    }
}